@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;800&family=Mukta:wght@200;400;800&family=Roboto+Condensed&display=swap');

html{
  width: 100%;
  height: 100%;
}

body{
  width: 100%;
  height: 100%;
  font-family: 'Montserrat', sans-serif;
  scroll-behavior: smooth;
}

*{
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}
a{
  text-decoration: none;
  color: gold;
}